import React, { useState, useEffect } from "react";
import Layout from './../../components/Layout/Layout.js';
import axios from "axios";
import { useParams, useNavigate } from "react-router-dom";
import html2canvas from "html2canvas";

const SingleCustomer = () => {
  const params = useParams();
  const navigate = useNavigate();
  const [customer, setCustomer] = useState({});

  useEffect(() => {
    if (params?.pid) getCustomer();
  }, [params?.pid]);

  const getCustomer = async () => {
    try {
      const { data } = await axios.get(`/api/v1/customer/get-customer/${params.pid}`);
      setCustomer(data?.customer);
    } catch (error) {
      console.log(error);
    }
  };

  const formatDate = (dateString) => {
    const date = new Date(dateString);
    return `${date.getDate()}/${date.getMonth() + 1}/${date.getFullYear()}`;
  };

  const renderDataRows = () => {
    return Object.entries(customer).map(([key, value], index) => {
      if (['_id', 'updatedAt', '__v'].includes(key)) return null;
      if (key === 'createdAt') value = formatDate(value);
      return (
        <tr key={index}>
          <td style={{ fontWeight: 'bold' }}>{key}</td>
          <td>{value}</td>
        </tr>
      );
    });
  };

  const downloadImage = async () => {
    const element = document.getElementById("capture");
    const canvas = await html2canvas(element);
    const data = canvas.toDataURL("image/jpg");
    const link = document.createElement('a');
    link.href = data;
    link.download = 'customer-details.jpg';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  return (
    <Layout>
      <div className="row container product-details" id="capture">
        <div className="col-md-6">
          {/* Placeholder for possible content */}
        </div>
        <div className="col-md-6 product-details-info">
          <h1 className="text-center">Client Details</h1>
          <hr />
          <table className="table mt-4">
            <tbody>
                {renderDataRows()}
            </tbody>
          </table>
          <img
            src={`/api/v1/customer/product-photo/${customer._id}`}
            className="card-img-top"
            alt={customer.name || 'Customer Photo'}
            height="300"
            width={"350px"}
          />
          <button onClick={downloadImage} className="btn btn-primary mt-3">Download as JPG</button>
        </div>
      </div>
      <hr />
    </Layout>
  );
};

export default SingleCustomer;
