import React, { useState, useEffect } from 'react';
import axios from 'axios';
import toast from 'react-hot-toast';
import { Link } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';

import Layout from './../../components/Layout/Layout.js';

const Customers = () => {
  const navigate = useNavigate();
  const [customers, setCustomers] = useState([]);
  const [filteredCustomers, setFilteredCustomers] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [selection, setSelection] = useState('all');
  const [InsuranceTypes, setInsuranceTypes] = useState([]);
  const [selectedInsuranceType, setSelectedInsuranceType] = useState('');
  const [LoanTypes, setLoanTypes] = useState([]);
  const [selectedLoanType, setSelectedLoanType] = useState('');

  const getAllCustomers = async () => {
    try {
      const { data } = await axios.get('/api/v1/customer/get-customer');
      setCustomers(data.customers);
      setFilteredCustomers(data.customers); // Initialize filtered customers
    } catch (error) {
      console.error(error);
      toast.error('Something went wrong');
    }
  };

  useEffect(() => {
    getAllCustomers();
  }, []);

  useEffect(() => {
    if (selection === 'Insurance' || selection === 'Loan') {
      extractTypes();
    }
    filterCustomers(); // Filter customers on every state change
  }, [searchTerm, selection, selectedInsuranceType, selectedLoanType]);
  
  useEffect(() => {
    // Dynamically extract types based on the current selection
    if (selection === 'Insurance' || selection === 'Loan') {
      extractTypes();
    }
    filterCustomers(); // Filter customers on every state change
  }, [searchTerm, selection, selectedInsuranceType, selectedLoanType]);

  const extractTypes = () => {
    if (selection === 'Insurance') {
      const uniqueTypes = new Set(customers.filter(customer => customer.Insurance_Type)
        .map(customer => customer.Insurance_Type));
      setInsuranceTypes([...uniqueTypes]);
      setSelectedLoanType(''); // Clear Loan type if switching to Insurance
    } else if (selection === 'Loan') {
      const uniqueTypes = new Set(customers.filter(customer => customer.Loan_Type)
        .map(customer => customer.Loan_Type));
      setLoanTypes([...uniqueTypes]);
      setSelectedInsuranceType(''); // Clear Insurance type if switching to loans
    }
  };
  

  const handleSearch = (e) => {
    setSearchTerm(e.target.value.toLowerCase());
  };

  const handleSelectChange = (event) => {
    setSelection(event.target.value);
    setSelectedInsuranceType('');
    setSelectedLoanType('');
  };

  const handleInsuranceTypeChange = (event) => {
    setSelectedInsuranceType(event.target.value);
  };

  const handleLoanTypeChange = (event) => {
    setSelectedLoanType(event.target.value);
  };

  const filterCustomers = () => {
    // Start by filtering based on the search term for Name or Phone
    let tempCustomers = customers.filter(customer => {
      const nameMatch = customer.Name ? customer.Name.toLowerCase().includes(searchTerm) : false;
      const phoneMatch = customer.Phone ? customer.Phone.includes(searchTerm) : false;
      return nameMatch || phoneMatch;
    });
  
    // Filter based on selection of Insurance or loans
    if (selection === 'Insurance') {
      tempCustomers = tempCustomers.filter(customer => customer.Insurance_Type);
      if (selectedInsuranceType) {
        // Further filter by specific Insurance type if selected
        tempCustomers = tempCustomers.filter(customer => customer.Insurance_Type === selectedInsuranceType);
      }
    } else if (selection === 'Loan') {
      tempCustomers = tempCustomers.filter(customer => customer.Loan_Type);
      if (selectedLoanType) {
        // Further filter by specific loan type if selected
        tempCustomers = tempCustomers.filter(customer => customer.Loan_Type === selectedLoanType);
      }
    }
  
    setFilteredCustomers(tempCustomers);
  };
  
    return (
    <Layout>
      <div className="container">
        <div className="row justify-content-center">
          <div className="col-lg-8">
            <h1 className="text-center">Customers List</h1>
            <div className="input-group mb-3">
              <select className="form-control" value={selection} onChange={handleSelectChange}>
                <option value="all">All</option>
                <option value="Insurance">Insurance</option>
                <option value="Loan">Loan</option>
              </select>
              {selection === 'Insurance' && InsuranceTypes.length > 0 && (
                <select className="form-control ml-2" value={selectedInsuranceType} onChange={handleInsuranceTypeChange}>
                  <option value="">Select Insurance Type</option>
                  {InsuranceTypes.map(type => (
                    <option key={type} value={type}>{type}</option>
                  ))}
                </select>
              )}
              {selection === 'Loan' && LoanTypes.length > 0 && (
                <select className="form-control ml-2" value={selectedLoanType} onChange={handleLoanTypeChange}>
                  <option value="">Select Insurance Type</option>
                  {LoanTypes.map(type => (
                    <option key={type} value={type}>{type}</option>
                  ))}
                </select>
              )}
              <input
                type="text"
                className="form-control ml-2"
                placeholder="Search by Name or Phone"
                onChange={handleSearch}
              />
              
            </div>
            <table className="table mt-4">
              <thead>
                <tr>
                  <th>Name</th>
                  <th>Phone</th>
                  <th>For</th>
                  <th>Action</th>
                </tr>
              </thead>
              <tbody>
                {filteredCustomers.map((customer) => (
                  <tr key={customer._id}>
                    <td>{customer.Name}</td>
                    <td>{customer.Phone}</td>
                    <td>{customer.option}</td>
                    
                    <td>
                     <button className='btn btn-primary ms-1'
                     onClick={()=>navigate(`/customers/single-customer/${customer._id}`)}
                     >
                       Details
                        </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default Customers;
