import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";

const Header = () => {
  const [navbarHeight, setNavbarHeight] = useState("20px");

  useEffect(() => {
    const nameImg = new Image();
    const logoImg = new Image();

    nameImg.src = "/images/name.jpeg";
    logoImg.src = "/images/mainlogo.jpeg";

    nameImg.onload = () => {
      setNavbarHeight("20px");
    };

    logoImg.onload = () => {
      setNavbarHeight("20px");
    };
  }, []);

  return (
    <>
      <nav
        className="navbar navbar-expand-lg bg-body-tertiary fixed-top"
        style={{
          height: "50px",
          display: "flex",
          alignItems: "center",
          padding: "0 25px",
        }}
      >
        <Link to="/" className="navbar-brand">
          <img
            src="/images/mainlogo.jpeg"
            className="banner-img"
            alt="bannerimage"
            style={{
              height: "50px",
              width: "auto",
            }}
          />
        </Link>
        <div>
          <Link to="/" className="navbar-brand">
            <img
              src="/images/name.jpeg"
              className="banner-img"
              alt="bannerimage"
              style={{
                height: "50px",
                width: "auto",
              }}
            />
          </Link>
        </div>
      </nav>
      <div style={{ marginTop: "60px" }}></div>
    </>
  );
};

export default Header;
